import { Component, OnInit, ViewChild } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { UtilsService } from 'src/app/services/utils.service';
import { AppValueService } from 'src/app/services/app-value.service';
import { TranslateService } from '@ngx-translate/core';
import { UserManagementService } from './user-management.service';
import { ModalEditUserComponent } from 'src/app/components/modal-edit-user/modal-edit-user.component';

@Component({
  selector: 'app-user-management',
  templateUrl: './user-management.page.html',
  styleUrls: ['./user-management.page.scss'],
})
export class UserManagementPage implements OnInit {
  @ViewChild('contentSearch') contentSearch: any;  
  heightTable = "775";
  constructor(private popoverController: PopoverController,
    public userService: UserManagementService,
    public utilsService: UtilsService,
    public appValueService: AppValueService,
    private translate: TranslateService) { }

  ngOnInit() {
  }
  showDetail(data: any, mode: any) {
    if(mode == "edit"){
      this.showModalEditUser(data.ev, data.row._row.data);
    }
    else {
      this.showModalEditUser(null, null);
    }    
  }
  async showModalEditUser(ev: any, row: any) {
    const popover = await this.popoverController.create({
      component: ModalEditUserComponent,
      id: "editUserModal",
      event: ev,
      translucent: true,
      componentProps: { rowProduct: row },
      cssClass: "edit-user-popover"
    });
    return await popover.present();
  }
  ionViewDidEnter() {
    this.heightTable = this.contentSearch.el.offsetHeight;
  }
}
